<template>
  <div class="content" v-swipeup="{fn:vuetouch,name:'上滑'}">
    <div class="head">
      <img src="../../../static/image/icon_back1.png" alt @click="back" />
      充值记录
    </div>
    <div class="main">
      <div class="all"></div>
      <ul class="list">
        <li v-for="(item,index) in list " :key="index">
          <div>
            <p class="title_">充值渠道</p>
            <p v-if="item.type ==1">支付宝</p>
            <p v-if="item.type ==2">微信</p>
            <p v-if="item.type ==3">银行卡</p>
          </div>
          <div>
            <p class="title_">充值金额</p>
            <p class="wrap">￥{{item.money | numFilter}}</p>
          </div>
          <div>
            <p class="title_">审核状态</p>
            <p v-if="item.status ==0">未审核</p>
            <p v-if="item.status ==1">审核通过</p>
            <p v-if="item.status ==2">审核未通过</p>
          </div>
          <div v-if="item.status ==2">
            <p class="title_">审核备注</p>
            <p>{{ item.content }}</p>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data() {
    return {
      list: [],
      page: 1
    };
  },
  components: {},

filters: {
  numFilter (value) {
    // 截取当前数据到小数点后两位
    let realVal = parseFloat(value).toFixed(2)
    return realVal
  }
},
  methods: {
    jiequs(str1, str2) {
      let str = str1 + str2;
      if (String(str).indexOf(".") > -1) {
        var temp = Number(str);
        temp = Math.floor(temp * 1000) / 1000;
        temp = temp.toFixed(4);
        return temp;
      } else {
        return str;
      }
    },
    getlist() {
      let that = this;
      that
        .$post("/apiv1/order/paylist", {
          page: that.page
        })
        .then(res => {
          console.log(res);
          if (res.code == 1) {
            if (res.data.length) {
              that.list = that.list.concat(res.data);
            } else {
              if (that.page > 1) {
                that.$msg("暂无更多数据");
              }
            }
          }
        });
    },
    back() {
      this.$router.go(-1);
    },
    vuetouch: function(s, e) {
      this.page++;
      this.getlist();
    }
  },
  created() {
    this.getlist();
  },
  destroyed() {}
};
</script>

<style lang="scss" scoped>
@import "../../assets/scss/mixin";
.content {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background: #0d1637;
  .head {
    width: 100%;
    height: 110px / $ppr;
    position: relative;
    text-align: center;
    line-height: 110px / $ppr;
    font-size: 36px / $ppr;
    color: #b2c1ff;
    background: #0d1637;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    img {
      width: 17px / $ppr;
      height: 29px / $ppr;
      display: block;
      position: absolute;
      top: 40px / $ppr;
      left: 44px / $ppr;
    }
  }
  .main {
    width: 675px / $ppr;
    margin: 0 auto;
    margin-top: 110px / $ppr;
    background: #3e4abf;
    border-radius: 15px / $ppr;
    box-sizing: border-box;
    .title {
      padding: 20px / $ppr 0;
      font-size: 24px / $ppr;
      color: #fff;
      padding-left: 30px / $ppr;
      box-sizing: border-box;
      background: #198bff;
      border-top-left-radius: 15px / $ppr;
      border-top-right-radius: 15px / $ppr;
    }
    .all {
      width: 100%;
      padding: 25px / $ppr 15px / $ppr 0 15px / $ppr;
      box-sizing: border-box;
      line-height: 50px / $ppr;
      .all_conte {
        padding: 0 15px / $ppr 15px / $ppr 15px / $ppr;
        display: flex;
        justify-content: space-between;
        border-bottom: 1px / $ppr solid #fff;
      }
      div {
        font-size: 30px / $ppr;
        color: #ffff;
      }
    }
    .list {
      width: 100%;
      padding: 0 15px / $ppr;
      box-sizing: border-box;
      font-size: 30px / $ppr;
      color: #fff;
      li {
        padding: 20px / $ppr 15px / $ppr;
        box-sizing: border-box;
        border-bottom: 1px / $ppr dotted #fff;
        div {
          width: 100%;
          display: flex;
          justify-content: space-between;
          text-align: center;
          line-height: 60px / $ppr;
          p{
            width: 50%;
          }
          p:nth-child(1){
            text-align: left;
          }
          p:nth-child(2){
            text-align: right;
          }
          .title_ {
            color: #b2c1ff;
          }
        }
      }
      li:last-child {
        border-bottom-style: none;
      }
    }
  }
}
</style>



